
function formatName(user) {
    return user.firstName + ' ' + user.lastName;
}

const name = 'James Moles';
const element = <h1>Hello, {name}!</h1>;

const user = {
    firstName: 'James',
    lastName: 'Moles',
};

const text = <h1>Hello, {formatName(user)}, goodbye!</h1>;

function Hello() {
  return text;
}

export default Hello;